.eh-notifications {
  position: absolute;
  top: 0;
  left: 50%;
  width: 600px;
  margin-left: -300px;
  padding-top: 6px;
}
.eh-notifications-group {
  min-height: 0;
  transition: all 0s 0.5s;
}
.eh-notifications-group._count,
.eh-notifications-group._history {
  min-height: 72px;
  background: #3d3935;
  border-radius: 3px;
  box-shadow: 0 12px 18px rgba(0,0,0,0.1);
  transition: all 0s 0s;
}
.eh-notifications-group._history:after {
  content: '';
  position: absolute;
  top: 30px;
  right: -12px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 12px 0 12px 12px;
  border-color: transparent transparent transparent #3d3935;
}
.eh-notifications-link {
  font-size: 12px;
  line-height: 36px;
  color: #b0a195;
  text-transform: uppercase;
  text-align: center;
  opacity: 0.5;
  cursor: pointer;
}
.eh-notifications-link:hover {
  opacity: 1;
}
.eh-notifications-button {
  position: absolute;
  top: 12px;
  bottom: 12px;
  right: 0;
  width: 72px;
  padding-top: 24px;
  font-size: 12px;
  line-height: 24px;
  color: rgba(176,161,149,0.5);
  text-align: center;
  cursor: pointer;
}
.eh-notifications-button:before {
  content: '';
  position: absolute;
  top: 0;
  right: 72px;
  width: 1px;
  height: 100%;
  background-image: linear-gradient(to bottom, rgba(176,161,149,0.5) 0%, rgba(176,161,149,0.5) 50%, rgba(176,161,149,0) 50%, rgba(176,161,149,0) 100%);
  background-size: 1px 2px;
  background-repeat: repeat-y;
}
.eh-notifications-button:after {
  content: '';
  position: absolute;
  top: 12px;
  left: 24px;
  width: 24px;
  height: 24px;
  background-image: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20fill%3D%22%23B0A195%22%20d%3D%22M.21%2023.79c.14.14.33.21.51.21.19%200%20.37-.07.51-.21l10.77-10.77%2010.76%2010.76c.14.14.33.21.51.21.19%200%20.37-.07.51-.21.28-.28.28-.74%200-1.02l-10.76-10.76%2010.77-10.76c.28-.28.28-.74%200-1.02s-.74-.28-1.02%200l-10.77%2010.76-10.76-10.77c-.28-.28-.74-.28-1.02%200s-.28.74%200%201.02l10.76%2010.77-10.77%2010.76c-.28.29-.28.74%200%201.03z%22/%3E%3C/svg%3E");
  opacity: 0.5;
}
