  top: 0;
  background-image: url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20fill%3D%22%23B0A195%22%20d%3D%22M12.22%200h-.22c-6.5%200-11.88%205.29-12%2011.78-.06%203.21%201.13%206.24%203.36%208.55%202.23%202.31%205.22%203.61%208.42%203.67h.22c6.5%200%2011.87-5.29%2012-11.78.11-6.62-5.17-12.1-11.78-12.22zm-.21%2023.14h-.21c-2.97-.05-5.75-1.26-7.81-3.4-2.06-2.14-3.17-4.96-3.12-7.93.1-6.04%205.1-10.95%2011.12-10.95h.21c6.14.11%2011.04%205.2%2010.94%2011.34-.11%206.03-5.11%2010.94-11.13%2010.94zm5.98-11.34l-5.58-5.79c-.17-.17-.44-.18-.61-.01-.17.16-.18.44-.01.61l4.86%205.04-10.33-.18h-.01c-.24%200-.43.19-.43.42%200%20.24.18.43.42.44l10.32.19-5.03%204.86c-.17.16-.17.44-.01.61.08.09.2.13.31.13.11%200%20.21-.04.3-.12l6.09-5.88-.29-.32z%22/%3E%3C/svg%3E");
