.eh-notifications-button:hover {
  color: #b0a195;
}
.eh-notifications-button:hover:after {
  opacity: 1;
}
.eh-notifications-message {
  position: relative;
  width: 100%;
  height: 0;
  overflow: hidden;
  opacity: 0;
  background: #3d3935;
  border-radius: 3px;
  box-shadow: 0 12px 18px rgba(0,0,0,0.1);
  transition: opacity 0.25s, height 0.25s 0.25s ease-out, margin 0.25s 0.25s ease-out;
}
.eh-notifications-group._count .eh-notifications-message,
.eh-notifications-group._history .eh-notifications-message {
  background: transparent;
  border-radius: 0;
  box-shadow: none;
}
.eh-notifications-group._history .eh-notifications-message:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 72px;
  right: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(176,161,149,0.25) 0%, rgba(176,161,149,0.25) 50%, rgba(176,161,149,0) 50%, rgba(176,161,149,0) 100%);
  background-size: 2px 1px;
  background-repeat: repeat-x;
}
.eh-notifications-message._visible {
  opacity: 1;
  margin-bottom: 12px;
  transition: opacity 0.25s 0.25s, height 0.25s ease-out, margin 0.25s ease-out;
}
.eh-notifications-group._history .eh-notifications-message._visible {
  margin-bottom: 0;
}
.eh-notifications-message>DIV {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 72px;
}
